<template>
    <div class="main-page">
        <div class="headerTag">车辆保养</div>
        <form class="h-form">
            <item must name="标题">
                <input v-model="info.FName" />
            </item>
            <item must name="车辆名称">
                <input v-model="info.CarName" readonly @click="choosePerson" />
            </item>
            <item must name="保养日期">
                <input v-model="info.FDate" readonly @click="chooseTime('FDate')" />
            </item>
        </form>
        <van-button type="primary" block size="normal" :color="$config.theme" style="width: calc(100% - 30px); margin:3px 15px" @click="submit" :loading="loading">提交</van-button>
        <van-popup v-model="timeShow" position="bottom">
            <van-datetime-picker type="date" title="选择日期" @confirm="confirmTime" />
        </van-popup>

    </div>
</template>

<script>
export default {
    name: "CarsMaintenancAE",
    props: {},
    components: {},
    data() {
        return {
            apis: {
                edit: this._api.VMS_Car_MaintenanceEdit,
                create: this._api.VMS_Car_MaintenanceCreate,
                list: this._api.VMS_Car_MaintenanceManage,
                delete: this._api.VMS_Car_MaintenanceDelete,
                audit: this._api.VMS_Car_MaintenanceAudit,
                details: this._api.VMS_Car_MaintenanceDetails
            },
            loading: false,
            curId: null,
            info: {
                PersonName: "",
                FName: "",
                EntryList: []
            },
            rules: {
                FName: "请输入标题！",
                CarId: "请选择车辆！",
                FDate: "请选择维修日期！"
            },
            curTimeFieldName: "",
            timeShow: false
        }
    },
    methods: {
        async submit() {
            await this.$u.checkForm(this.info, this.rules)
            this.loading = true
            let apiFun = this.curId ? this.apis.edit : this.apis.create
            apiFun(this.info).then(res => {
                if (res) {
                    this.$u.msg(this.info.Id ? "编辑成功！" : "创建成功！")
                    setTimeout(() => {
                        this.$router.back()
                    }, 200);
                }
                this.loading = false
            })
        },
        chooseTime(fieldName) {
            this.curTimeFieldName = fieldName
            this.timeShow = true
        },
        confirmTime(time) {
            this.timeShow = false
            this.info[this.curTimeFieldName] = this.$time(time).format("YYYY-MM-DD")
        },
        choosePerson() {
            window.temFun = (car) => {
                this.$set(this.info, "CarName", car.FCarNo)
                this.$set(this.info, "CarId", car.Id)
            }
            this.$router.push(`/modular/oa/CarsChoose`)
        }
    },
    mounted() {
        this.info = {}
        this.curId = this.$route.query.Id
    }
}
</script>

<style lang="scss" >
.car-item {
    width: calc(100% - 30px);
    margin-left: 15px;
    margin-top: 15px;
    padding: 0 15px;
    font-size: 14px;

    .car-item-line {
        line-height: 30px;
        margin-right: 15px;
    }
}
</style>